.chat {
    padding: 30px
}

.messages {
    display: flex;
    flex-direction: column-reverse;
    width: 400px;
    height: 600px;
    background: white;
    overflow: auto;
    direction: revert;
}

.message {
    color: #3333;
    padding: 15px;
    opacity: 0;
    animation: show 1s 1; /* Название, прододжительность, к-ство повторений */
    animation-fill-mode: forwards; /* Чтобы элемент оставался в конечном состоянии анимации */
    animation-delay: 0.1s; /* Задержка перед началом */
}

@keyframes show {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}